<template>
  <el-card class="auth-search">
    <el-header>
      路由查询
    </el-header>
<!--高级搜索 按照下列来
    <el-table-column  prop="id"  label="ID"  width="180"></el-table-column>
    <el-table-column  prop="fid"  label="服务名称"></el-table-column>
    <el-table-column  prop="uri"  label="URI"></el-table-column>
    <el-table-column  prop="route"  label="路由"></el-table-column>
    <el-table-column  prop="authRole"  label="角色"></el-table-column>
    <el-table-column  prop="operUser"  label="上一次操作UserAccount"></el-table-column>
    <el-table-column  prop="checkLogin"  label="是否仅检查登录"></el-table-column>
    <el-table-column  prop="isDef"  label="是否默认"></el-table-column>
    <el-table-column  prop="isOpen"  label="是否开启"></el-table-column>-->

    <el-form :inline="true" :model="searchForm" class="demo-form-inline">
      <el-form-item label="ID">
        <el-input v-model="searchForm.id" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="服务名称">
        <el-input v-model="searchForm.serviceName" placeholder="服务名称"></el-input>
      </el-form-item>
      <el-form-item label="请求方式">
        <el-select v-model="searchForm.method" placeholder="请求方式">
          <el-option label="GET" value="GET"></el-option>
          <el-option label="POST" value="POST"></el-option>
          <el-option label="PUT" value="PUT"></el-option>
          <el-option label="DELETE" value="DELETE"></el-option>
          <el-option label="PATCH" value="PATCH"></el-option>
          <el-option label="HEAD" value="HEAD"></el-option>
          <el-option label="OPTIONS" value="OPTIONS"></el-option>
          <el-option label="TRACE" value="TRACE"></el-option>
          <el-option label="CONNECT" value="CONNECT"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="URI">
        <el-input v-model="searchForm.uri" placeholder="URI"></el-input>
      </el-form-item>
      <el-form-item label="路由">
        <el-input v-model="searchForm.route" placeholder="路由"></el-input>
      </el-form-item>
      <el-input v-model="currentTag" placeholder="角色" @keyup.native.enter="addTag" @keydown.native.delete="delTag">
      </el-input>
      <el-tag
          v-for="tag in searchForm.authRole"
          :key="tag"
          closable
          @close="handleCloseTag(item)"
          :disable-transitions="false">{{tag}}</el-tag>
      <el-form-item label="上一次操作UserAccount">
        <el-input v-model="searchForm.operUser" placeholder="上一次操作UserAccount"></el-input>
      </el-form-item>
      <el-form-item label="是否仅检查登录">
        <el-select v-model="searchForm.checkLogin" placeholder="是否仅检查登录">
          <el-option label="是" value="true"></el-option>
          <el-option label="否" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="是否非注解鉴权">
        <el-select v-model="searchForm.isDef" placeholder="是否非注解鉴权">
          <el-option label="是" value="true"></el-option>
          <el-option label="否" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="是否开启">
        <el-select v-model="searchForm.isOpen" placeholder="是否开启">
          <el-option label="是" value="true"></el-option>
          <el-option label="否" value="false"></el-option>
        </el-select>
      </el-form-item>
      <el-button
          type="primary"
          icon="el-icon-search"
          @click="search"
      >查询</el-button>
    </el-form>
  </el-card>
</template>

<script>
import {AuthApi} from "@/core/request/api/AuthApi";

export default {
  name: "auth-search",
  data(){
    return {
      currentTag:null,
      searchForm: {
        uri: '',
        serviceName: '',
        method: '',
        route: '',
        authRole: '',
        operUser: '',
        checkLogin: '',
        isDef: '',
        isOpen: '',
        current:1,
        pageSize:10
      }
    }
  },
  methods: {
    handleCloseTag(item){
      this.searchForm.authRole.splice(this.searchForm.authRole.indexOf(item), 1);
    },
    addTag(){
      this.searchForm.authRole.push(this.currentTag);
      this.currentTag=null;
    },
    delTag(){
      if (this.currentTag===null){
        this.searchForm.authRole.splice(this.searchForm.authRole.length-1, 1)
      }
    },
    search(){
      AuthApi.page(this.searchForm).then(res=>{
        if (!res.data.code){
          this.$parent.datas=res.data.data.records
        }
      })
    }
  }
}
</script>

<style scoped>

</style>