import {request} from "@/core/AxiosConfig";

const base='';
export const AuthApi= {
    //获取用户信息
    getInfo:`${base}/get/usinfo`,


    reload: `${base}/reload`,

    list:`${base}/list`,
    open:`${base}/open`,
    close:`${base}/close`,
    del:(data) => {
        return request.delete(`${base}/`,{data});
    },

    page:(params) =>{
        return request.get(`${base}/page`,{params});
    },
    add:(data) => {
        return request.post(`${base}/`,data);
    }
}