<template>
<div style="text-align: center;">
@{{Time}} <br>PoweredBy <br>{{SiteName}}
</div>
</template>

<script>

import {SiteApi} from "@/core/request/api/SiteApi";
import {globalValue} from "@/core/config/globalConfig";

export default {
  name: "globelFooter",
  data(){
    return{
      Time:null,
      SiteName:null
    }
  },
  mounted() {
    var fullYear = new Date().getFullYear();
    this.axios.get(SiteApi.getAttributeByCommon+"publishTime")
        .then(res=>{
          if (!res.data.code&&res.data.data == fullYear){
            this.Time=res.data.data
          }
          else {
            this.Time=fullYear.toString()
          }
        })
        .catch(()=>{
          this.Time=fullYear.toString()
        })
    this.axios.get(SiteApi.getAttributeByCommon+"siteName")
        .then(res=>{
          if (!res.data.code){
            this.SiteName=res.data.data
          }
          else {
            this.SiteName=globalValue.protectTitle()
          }
        })
        .catch(()=>{
          this.SiteName=globalValue.protectTitle()
        })
  }
}
</script>

<style scoped>
.div{
    bottom: 0px;
}
</style>
